import './App.css';
import {useEffect, useState} from "react";
import datarank from "./photos/datarank.png";
import white from "./photos/white.jpeg";
import HashLoader from "react-spinners/HashLoader";

const BASE_URI = ".";

function getType() {
    const SELECT = document.getElementById("type");
    // eslint-disable-next-line no-unused-expressions
    const SELECTION = SELECT.options[SELECT.selectedIndex].text;
    switch (SELECTION) {
        case "기사":
            return "news";
        case "노래":
            return "music";
        default:
            break;
    }
}

function getImageURL() {
    const ENCODE_QUERY = encodeURIComponent(document.getElementById("input").value);
    const TYPE = getType();
    return `${BASE_URI}/${TYPE}?query=${ENCODE_QUERY}`;
}

function App() {
    const [width, setwidth]=useState("100%");
    const [image_url, set_image_url] = useState(white);
    const [on_load, set_on_load] = useState(false);

    return(
        <div className={"mainbox"}>
            <img src={datarank} width={200}/>
            <div className={"subbox"}>
                <select id={"type"}>
                    <option selected={true}>기사</option>
                    <option>노래</option>
                </select>
                <input id={"input"} style={{width: `${width}`}} onInput={()=>{
                    let value=document.getElementById("input").value;
                    setwidth(value===""?"100%":`${50+12*value.length}px`);
                }} onKeyUp={()=>{
                    if(window.event.keyCode === 13) {
                        if(image_url === getImageURL()) return;
                        set_on_load(true);
                        set_image_url(getImageURL());
                    }
                }}/>
                <div className={"btn"} onClick={() => {
                    if(image_url === getImageURL()) return;
                    set_on_load(true);
                    set_image_url(getImageURL());
                }}>검색</div>
            </div>
            <div className={"box"}>
                <HashLoader size={40} color={"silver"} />
                <img src={image_url} className={"img"} onLoad={()=>set_on_load(false)} style={{
                    display: on_load? "none":"inline"
                }} />
            </div>
        </div>
    )
}

export default App;